<template>
  <el-dialog :title="title" :visible.sync="dialogFormVisible" width="600px">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input
          :disabled="disabled"
          v-model="ruleForm.name"
          placeholder=""
          maxlength="12"
          show-word-limit
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="telephone">
        <el-input
          :disabled="disabled"
          v-model="ruleForm.telephone"
          placeholder=""
          style="width: 200px"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="post">
        <el-input
          :disabled="disabled"
          v-model="ruleForm.post"
          style="width: 200px"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="address.area">
        <el-cascader :disabled="disabled" v-model="ruleForm.address.area" :options="options"></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="address.details">
        <el-input
          :disabled="disabled"
          type="textarea"
          v-model="ruleForm.address.details"
          style="width: 400px"
          placeholder="100"
        ></el-input>
      </el-form-item>
      <el-form-item style="margin:30px 0 0 100px;" v-if="!disabled">
        <el-button @click="resetForm('ruleForm')"></el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import options from "../assets/city";
export default {
  data() {
    const checkName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (!value.match(/^[a-zA-Z0-9_\-\u4e00-\u9fa5]{1,12}$/)) {
          callback(new Error("1-12_"));
        } else {
          callback();
        }
      }, 300);
    };
    const checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (
          !value.match(
            /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/
          )
        ) {
          callback(new Error("11"));
        } else {
          callback();
        }
      }, 300);
    };
    return {
      dialogFormVisible: false,
      ruleForm: {
        name: "",
        telephone: "",
        post: "",
        address: {
          area: [],
          details: ""
        }
      },
      formLabelWidth: "120px",
      rules: {
        name: [{ required: true, validator: checkName, trigger: "blur" }],
        telephone: [{ required: true, validator: checkPhone, trigger: "blur" }],
        "address.area": [{ required: true, trigger: "blur" }],
        "address.details": [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 100,
            message: "100",
            trigger: "blur"
          }
        ]
      },
      value: [],
      title: "",
      disabled: false,
      openWay: "",
      options: options
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.openWay === "add") {
            this.axios
              .post("/address/add", this.ruleForm)
              .then(result => {
                if (result.data.status === 1) {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.$emit("initParent");
                  this.dialogFormVisible = false;
                } else {
                  this.$message.error("");
                }
              })
              .catch(err => {
                this.$router.push("/*");
              });
          } else {
            this.axios
              .post("/address/update", {
                _id: this.ruleForm._id,
                name: this.ruleForm.name,
                telephone: this.ruleForm.telephone,
                post: this.ruleForm.post,
                address: {
                  area: this.ruleForm.address.area,
                  details: this.ruleForm.address.details
                }
              })
              .then(result => {
                if (result.data.status === 1) {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.$emit("initParent");
                  this.dialogFormVisible = false;
                } else {
                  this.$message.error("");
                }
              })
              .catch(err => {
                this.$router.push("/*");
              });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleChange(value) {
      console.log(value);
    },
    init(addressInfo) {
      this.ruleForm = addressInfo;
    },
    isShow(way) {
      this.openWay = way;
      this.disabled = false;
      if (way === "add") {
        this.ruleForm = {
          name: "",
          telephone: "",
          post: "",
          address: {
            area: [],
            details: ""
          }
        };
        this.title = "";
      } else if (way === "query") {
        this.title = "";
      } else {
        this.title = "";
      }
      if (way === "query") {
        this.disabled = true;
      }
      this.dialogFormVisible = !this.dialogFormVisible;
    }
  }
};
</script>

<style lang="scss" scoped>
.el-input {
  width: 300px;
}
</style>
